import keys from './keys.json' // TODO   

const scope = 'https://www.googleapis.com/auth/spreadsheets';
//  
const state = '4a2bd1c6-a672-4b26-8b1c-70ef8460a279'
const responseType = {
  //   authorization code
  code: 'code',
  //   access token
  token: 'token'
}

/**
 *  URL   access token.
 *      URL.
 *
 * @see https://developers.google.com/identity/protocols/oauth2/web-server#creatingclient
 */
export function getAuthenticatedClient() {
  const redirectUriIndex = process.env.NODE_ENV === 'production' ? 1 : 0;
  const url = `https://accounts.google.com/o/oauth2/v2/auth?
    client_id=${keys.web.client_id}&
    response_type=${responseType.token}&
    state=${state}&
    scope=${scope}&
    redirect_uri=${encodeURIComponent(keys.web.redirect_uris[redirectUriIndex])}&
    prompt=consent&
    include_granted_scopes=true
  `;
  const urlNext = url.replace(/\r\n|\s+/g, '');
  console.log('!!-!!-!!  urlNext {221120171248}\n', urlNext); // del+
  // ---
  window.location.href = urlNext;
}
